export default {
	data() {
		return {
			imgUrl: this.$imgUrl,
			courseList: [],
		}
	},
	onLoad:function(option) {
		this.gettingData(option.id)
	},
	methods: {
		gettingData (id) {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$http.post('System/get_courses_details',
					'\r\n--XXX' +
					'\r\nContent-Disposition: form-data; name="id"' +
					'\r\n' +
					'\r\n' + id +
					'\r\n--XXX--').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.courseList = res.data.data
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
	}
}
